/** @format */

import CssBaseline from "@material-ui/core/CssBaseline";
import { MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import axios from "axios";
import "fontsource-roboto";
import React, { useEffect, useState } from "react";
import { getCurrentWeek } from "./dates";
import DayDetail from "./DayDetail";
import { useDays } from "./Days";
import FindGrid from "./FindGrid";
import { getFindCountData } from "./GetData";
import NextWeek from "./NextWeek";
import {
  getConfigURL,
  getFindsURL,
  getHolidaysURL,
  getServerVersionURL,
} from "./Server";
import { Themes } from "./Themes";
import { useCustomTheme } from "./useCustomTheme";
import { version } from "./Version";

const App = () => {
  const [theme, setTheme, componentMounted] = useCustomTheme(Themes.Green);
  const defaultDays = 21;

  const [days, setDays] = useDays(defaultDays);
  const [open, setOpen] = useState(false);
  const [selectedValue, setSelectedValue] = useState("");

  const [toDoOpen, setToDoOpen] = useState(false);
  const [toDoWeek, setToDoWeek] = useState({ dates: [], goal: 0, number: 0 });

  const [clickedDate, setClickedDate] = useState("");
  const [position, setPosition] = useState({
    status: "fail",
    latitude: 0,
    longitude: 0,
  });

  const [allData, setAllData] = useState({});
  const [dataLoaded, setDataLoaded] = useState(false);
  const [holidays, setHolidays] = useState({});
  const [serverVersion, setServerVersion] = useState({ version: "0.0.0" });
  const [config, setConfig] = useState({
    pq_time: "",
    update_time: "",
    goal: 4,
  });

  useEffect(() => {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((pos) => {
        setPosition({
          status: "ok",
          latitude: pos.coords.latitude,
          longitude: pos.coords.longitude,
        });
      });
    }

    // get the find data
    axios.get(getFindsURL()).then((res) => {
      setAllData(res.data);
      setDataLoaded(true);
    });

    // get the holidays
    axios.get(getHolidaysURL()).then((res) => {
      setHolidays(res.data);
    });

    // get the server version
    axios.get(getServerVersionURL()).then((res) => {
      setServerVersion(res.data);
    });

    // get the configuration information
    axios.get(getConfigURL()).then((res) => {
      setConfig(res.data);
      console.log(res.data);
    });
  }, []);

  const changeTheme = (event) => {
    setTheme(event.target.value);
  };

  const changeDays = (event) => {
    setDays(event.target.value);
  };

  const dayOnClick = (cell) => {
    if (cell.day !== "") {
      setClickedDate(cell);
      setOpen(true);
    }
  };

  const dayDlgClose = (value) => {
    setOpen(false);
    setSelectedValue(value);
  };

  const handleWeek = () => {
    const week = getCurrentWeek(allData);
    // console.log(week);

    setToDoWeek(week);
    setToDoOpen(true);
  };

  const toDoDlgClose = (value) => {
    setToDoOpen(false);
  };

  if (!componentMounted || !dataLoaded) {
    return <div />;
  }

  const fcd = getFindCountData(days, allData);
  const summary = {
    generated: new Date(config.pq_time).toLocaleString(),
    goal: config.goal,
    next7days: fcd.summary.next7days,
  };

  return (
    <>
      <MuiThemeProvider theme={theme}>
        <CssBaseline />
        <FindGrid
          data={fcd.data}
          summary={summary}
          changeTheme={changeTheme}
          changeDays={changeDays}
          dayOnClick={dayOnClick}
          days={days}
          handleWeek={handleWeek}
          holidays={holidays}
        />
        <Typography
          align="right"
          variant="subtitle2"
          style={{ paddingRight: "10px" }}>
          {version}/{serverVersion.version}
        </Typography>
        <DayDetail
          selectedValue={selectedValue}
          open={open}
          cell={clickedDate}
          onClose={dayDlgClose}
          position={position}
          allData={allData}
        />

        <NextWeek week={toDoWeek} open={toDoOpen} onClose={toDoDlgClose} />
      </MuiThemeProvider>
    </>
  );
};

export default App;
